import React from 'react'
import { Navbar, NavDropdown, Nav, Container } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useNavigate } from 'react-router-dom'


import './NavBar.css'
const NavBar = (props) => {
  const navigate = useNavigate()


  if (props.user) {
    var user = props.user.data
  }


  const logout = () => {
    console.log("logout Reached");
    localStorage.clear("userInfo");
    console.log("dddddddddddd");

    navigate('/login');
  };

  return (
    <Navbar className='px-5' bg="white" expand="lg">
      <Container fluid>

       
        <Navbar.Brand href="#">Fitness</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '150px' }}
            navbarScroll
          >
            <Nav.Link style={{ marginLeft: "110px" }} href="">Free Workouts</Nav.Link>
            <LinkContainer to={'/program'}>

              <Nav.Link className='mx-3' >Programs</Nav.Link>
            </LinkContainer>
            <Nav.Link className='mx-3' href="">Blogs</Nav.Link>
            <Nav.Link className='mx-3' href="">About</Nav.Link>
            {!user&& <LinkContainer to={'/login'}>
              <Nav.Link className='mx-3' href="">Login</Nav.Link>

            </LinkContainer>}
           
            {user&&<Nav.Link onClick={()=>{logout()}} to={"/"} >Logout</Nav.Link>}

           {!user&& <LinkContainer to={'/signup'}>
              <Nav.Link className='mx-3' href="">sigunup</Nav.Link>
            </LinkContainer>
}
          </Nav>
          {/* <Form className="d-flex">
          <FormControl
            type="search"
            placeholder="Search"
            className="me-2"
            aria-label="Search"
          />
          <Button variant="outline-success">Search</Button>
        </Form> */}
          <Nav.Link className='text-danger'>{user ? user.name : ""}</Nav.Link>
          <NavDropdown title="Link" id="navbarScrollingDropdown">
            <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
            <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action5">
              Something else here
            </NavDropdown.Item>
          </NavDropdown>
          <Nav.Link href="#" disabled>
            Link
          </Nav.Link>


        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default NavBar
